/*
* overlayState()enterAnime()leaveAnime()
* animate.cssanimate
*/
<template>
  <div class="overlay_main">
    <transition :enter-active-class="enterAnime" :leave-active-class="leaveAnime">
      <div class="overlay" v-show="overlayState">
        <div class="pop">
          <slot></slot>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import 'animate.css'
export default {
  props: {
    overlayState: false,
    enterAnime: {
      type: String,
      default: 'animated slideInUp'
    },
    leaveAnime: {
      type: String,
      default: 'animated slideOutDown'
    }
  }
}

</script>
<style lang="scss" scoped>
.overlay_main {
  width: 100%;
  height: 100%;
  overflow: hidden;

  .overlay {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99;

    .pop {
      width: fit-content;
      height: fit-content;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
  }
}

.roll-enter-active {
  animation: roll-in 0.5s;
}

.roll-leave-active {
  animation: roll-in 0.5s reverse;
}

@keyframes roll-in {
  from {
    top: 100%;
  }

  to {
    top: 0;
  }
}

.fade-enter-active {
  animation: roll-in 0.5s;
}

.fade-leave-active {
  animation: roll-in 0.5s reverse;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

</style>



