/*
* 
*/
<template>
  <div class="main">
    <div class="main_area" :class="resetPull ? 'reset_pull' : ''">
      <div class="pull_area">
        <div class="pull_text">
          <i class="fa" :class="iconUp ? 'fa-arrow-up' : 'fa-arrow-down'"></i>
          <span>{{msg}}</span>
        </div>
      </div>
      <slot></slot>
    </div>
</template>
<script>
export default {
  data() {
    return {
      msg: '', // 
      resetPull: false, // 
      iconUp: false // 
    }
  },
  mounted() {
    let scroll = document.querySelector('.main_area')
    let outerScroller = document.querySelector('.main')
    let touchStart = 0
    let touchDis = 0
    let _this = this
    outerScroller.addEventListener('touchstart', function(event) {
      let touch = event.targetTouches[0]
      // 
      touchStart = touch.pageY
    }, false)
    outerScroller.addEventListener('touchmove', function(event) {
      let touch = event.targetTouches[0]
      let top = scroll.offsetTop + touch.pageY - touchStart
      if (top > 99) { // 100px
        top = 100
      }
      if (top > 70) { // 70
        _this.iconUp = true
        _this.msg = ''
      } else if (top > 0) { // 70
        _this.iconUp = false
        _this.msg = ''
      } else {
        top = 0
      }
      scroll.style.top = top + 'px'
      touchStart = touch.pageY
      touchDis = touch.pageY - touchStart
    }, false)
    outerScroller.addEventListener('touchend', function(event) {
      touchStart = 0
      let top = scroll.offsetTop
      if (top > 70) { // 70refresh
        _this.refresh(scroll)
      } else { // 70
        _this.resetPull = true
        setTimeout(() => { // 
          _this.resetPull = false
          scroll.style.top = 0
          _this.iconUp = false
          _this.msg = ''
        }, 500)
      }
    }, false)
  },
  methods: {
    refresh(scroll) {
      this.$emit('refresh')
      this.resetPull = true
      setTimeout(() => { // 
        this.resetPull = false
        scroll.style.top = 0
        this.iconUp = false
        this.msg = ''
      }, 1000)
    }
  }
}

</script>
<style lang="scss" scope>
.main {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  left: 0;
  overflow: auto;

  .main_area {
    width: 100%;
    margin-top: 0;
    position: absolute;
    left: 0;
    padding: 0;
    top: 0;

    .pull_area {
      width: 100%;
      height: 30px;
      margin-top: -30px;

      .pull_text {
        color: #cccccc;
        text-align: center;
        height: 100%;
        line-height: 100%;
      }

      .pull_icon {
        transition: all 0.5s;
      }

      p {
        font-size: 12px;
      }
    }
  }

  .reset_pull {
    animation: resetPull 1s forwards;
  }
}

@keyframes resetPull {
  from {
    /*top: 100px;*/
  }

  to {
    top: 0;
  }
}

</style>



